.jp-audio *:focus,
.jp-audio-stream *:focus,
.jp-video *:focus {
  outline:none;
}

.jp-audio button::-moz-focus-inner,
.jp-audio-stream button::-moz-focus-inner,
.jp-video button::-moz-focus-inner {
  border: 0;
}

.jp-audio,
.jp-audio-stream,
.jp-video {
  font-size:16px;
  font-family:Verdana, Arial, sans-serif;
  line-height:1.6;
  color: #666;
  /*border:1px solid #009be3;*/
  /*background-color:#eee;*/
}
.jp-audio {
  width:420px;
}
.jp-audio-stream {
  width:182px;
}
.jp-video-270p {
  width:380px;
}
.jp-video-360p {
  width:640px;
}
.jp-video-full {
  /* Rules for IE6 (full-screen) */
  width:480px;
  height:270px;
  /* Rules for IE7 (full-screen) - Otherwise the relative container causes other page items that are not position:static (default) to appear over the video/gui. */
  position:static !important; position:relative;
}

/* The z-index rule is defined in this manner to enable Popcorn plugins that add overlays to video area. EG. Subtitles. */
.jp-video-full div div {
  z-index:1000;
}

.jp-video-full .jp-jplayer {
  top: 0;
  left: 0;
  position: fixed !important; position: relative; /* Rules for IE6 (full-screen) */
  overflow: hidden;
}

.jp-video-full .jp-gui {
  position: fixed !important; position: static; /* Rules for IE6 (full-screen) */
  top: 0;
  left: 0;
  width:100%;
  /*height:100%;*/
  z-index:1001; /* 1 layer above the others. */
}

.jp-video-full .jp-interface {
  position: absolute !important; position: relative; /* Rules for IE6 (full-screen) */
  bottom: 0;
  left: 0;
}

.jp-interface {
  position: relative;
  background-color:#000000;
  background-color: rgba(51,51,51,0.65);
  height:37px;
  width:100%;
}

.jp-audio .jp-interface {
  height:80px;
}

.jp-audio-stream .jp-interface {
  height:80px;
}

.jp-video .jp-interface {
  /*border-top:1px solid #009be3;*/
  top:-37px;
}

/* @group CONTROLS */

.jp-controls-holder {
  clear: both;
  float: left;
  margin:0 auto;
  position: relative;


}

.jp-interface .jp-controls {
  margin:0;
  padding: 0;
  overflow:hidden;
}

.jp-audio .jp-controls {
  width: 380px;
  padding:20px 20px 0 20px;
}

.jp-audio-stream .jp-controls {
  position:absolute;
  top:20px;
  left:20px;
  width:142px;
}

.jp-video .jp-type-single .jp-controls {
  width: 39px;
  height: 37px;
  border-right: 1px solid #999999;
}

.jp-video .jp-type-playlist .jp-controls {
  width: 134px;
  margin-left: 172px;
}
.jp-video .jp-controls {
  float: left;
}

.jp-controls button {
  display:block;
  float: left;
  margin: 11px auto 0 14px;
  overflow:hidden;
  text-indent:-9999px;
  border:none;
  cursor:pointer;
}
.jp-play {
  width:17px;
  height:17px;
  margin: 0;
  padding: 0;

}

.jp-play {
  background: url("jplayer-nr.png") -39px -32px no-repeat;
}
.jp-play:focus {
  background: url("jplayer-nr.png") -39px -32px no-repeat;
}
.jp-state-playing .jp-play {
  background: url("jplayer-nr.png") -9px -32px no-repeat;
}
.jp-state-playing .jp-play:focus {
  background: url("jplayer-nr.png") -9px -32px no-repeat;
}

.jp-stop, .jp-previous, .jp-next {
  width:28px;
  height:28px;
  margin-top:6px;
}

.jp-stop {
  background: url("jplayer.blue.monday.jpg") 0 -83px no-repeat;
  margin-left:10px;
}

.jp-stop:focus {
  background: url("jplayer.blue.monday.jpg") -29px -83px no-repeat;
}

.jp-previous {
  background: url("jplayer.blue.monday.jpg") 0 -112px no-repeat;
}
.jp-previous:focus {
  background: url("jplayer.blue.monday.jpg") -29px -112px no-repeat;
}

.jp-next {
  background: url("jplayer.blue.monday.jpg") 0 -141px no-repeat;
}
.jp-next:focus {
  background: url("jplayer.blue.monday.jpg") -29px -141px no-repeat;
}

/* @end */

/* @group progress bar */

.jp-progress {
  overflow:hidden;
  background-color: #ddd;
}
.jp-audio .jp-progress {
  position: absolute;
  top:32px;
  height:15px;
}
.jp-audio .jp-type-single .jp-progress {
  left:110px;
  width:186px;
}
.jp-audio .jp-type-playlist .jp-progress {
  left:166px;
  width:130px;
}
.jp-video .jp-progress {
  margin-left: 10px;
  width:120px;
  height:4px;
  margin-top: 17px;
  float: left;
}
.jp-seek-bar {
  background: url("jplayer.blue.monday.jpg") 0 -202px repeat-x;
  width:0px;
  height:100%;
  cursor: pointer;
}
.jp-play-bar {
  /*background: url("jplayer.blue.monday.jpg") 0 -218px repeat-x ;*/
  width:0px;
  height:100%;
  background-color: #ff4850;
}

/* The seeking class is added/removed inside jPlayer */
.jp-seeking-bg {
  background: url("jplayer.blue.monday.seeking.gif");
}

/* @end */

/* @group volume controls */

.jp-state-no-volume .jp-volume-controls {
  display:none;
}

.jp-volume-controls {
  /*position:absolute;*/
  /*top:32px;
  left:308px;
  width:200px;*/
}
.jp-audio-stream .jp-volume-controls {
  left:70px;
}
.jp-video .jp-volume-controls {
  float: left;
  position: absolute;
  right: 37px;
  border-left:1px solid #999999;
  height: 100%;
  width: 42px;
}

.jp-volume-controls button {
  display:block;
  margin: 10px;
  padding: 0;
  overflow:hidden;
  text-indent:-9999px;
  border:none;
  cursor:pointer;
}

.jp-mute,
.jp-volume-max {
  width:21px;
  height:21px;
}
.jp-volume-max {
  left:74px;
}

.jp-mute {
  background: url("jplayer.blue.monday.jpg") 0 -170px no-repeat;
}
.jp-mute:focus {
  background: url("jplayer.blue.monday.jpg") -19px -170px no-repeat;
}
.jp-state-muted .jp-mute {
  background: url("jplayer.blue.monday.jpg") -60px -170px no-repeat;
}
.jp-state-muted .jp-mute:focus {
  background: url("jplayer.blue.monday.jpg") -79px -170px no-repeat;
}
.jp-volume-max {
  background: url("jplayer-nr.png") -5px -6px no-repeat;
}
.jp-volume-max:focus {
  background: url("jplayer-nr.png") -5px -6px no-repeat;
}
.jp-volume-bar-wrap{
  background-color: rgba(51, 51, 51, 0.65);
  position: absolute;
  width: 40px;
  height: 76px;
  top:-76px;
  display: none;
}
/*.jp-volume-bar {
	position: absolute;
	overflow:hidden;


}*/
.jp-volume-bar {
  position:relative;
  background-color: #999999;
  top:9px;
  left:19px;
  width:5px;
  height:60px;
  cursor: pointer;

}
.jp-volume-bar-value {
  position:absolute;
  bottom: 0;
  width: 5px;
  background-color: #ff4850;
}
/*.jp-volume-bar-value {
	background: url("jplayer.blue.monday.jpg") 0 -256px repeat-x;
	width:0px;
	height:5px;
}*/

/* @end */

/* @group current time and duration */

.jp-audio .jp-time-holder {
  position:absolute;
  top:50px;
}
.jp-audio .jp-type-single .jp-time-holder {
  left:110px;
  width:186px;
}
.jp-audio .jp-type-playlist .jp-time-holder {
  left:166px;
  width:130px;
}
.jp-time-wrap{
  float: left;
  margin: 3px 0 0 13px;
  color: #fff;
}

.jp-current-time,
.jp-duration,.jp-time-wrap span {
  font-size:.64em;
}
.jp-current-time {
  cursor:default;
}
.jp-duration {

  display:inline;
  text-align: right;
  cursor:pointer;
}

.jp-video .jp-current-time {

}
.jp-video .jp-duration {

}

/* @end */

/* @group playlist */

.jp-details {
  font-weight:bold;
  text-align:center;
  cursor:default;
}

.jp-details,
.jp-playlist {
  width:100%;
  background-color:#ccc;
  border-top:1px solid #009be3;
}

.jp-type-single .jp-details,
.jp-type-playlist .jp-details {
  border-top:none;
}

.jp-details .jp-title {
  margin:0;
  padding:5px 20px;
  font-size:.72em;
  font-weight:bold;
}

.jp-playlist ul {
  list-style-type:none;
  margin:0;
  padding:0 20px;
  font-size:.72em;
}
.jp-playlist li {
  padding:5px 0 4px 20px;
  border-bottom:1px solid #eee;
}
.jp-playlist li div {
  display:inline;
}

/* Note that the first-child (IE6) and last-child (IE6/7/8) selectors do not work on IE */

div.jp-type-playlist div.jp-playlist li:last-child {
  padding:5px 0 5px 20px;
  border-bottom:none;
}
div.jp-type-playlist div.jp-playlist li.jp-playlist-current {
  list-style-type:square;
  list-style-position:inside;
  padding-left:7px;
}
div.jp-type-playlist div.jp-playlist a {
  color: #333;
  text-decoration: none;
}
div.jp-type-playlist div.jp-playlist a:hover {
  color:#0d88c1;
}
div.jp-type-playlist div.jp-playlist a.jp-playlist-current {
  color:#0d88c1;
}

div.jp-type-playlist div.jp-playlist a.jp-playlist-item-remove {
  float:right;
  display:inline;
  text-align:right;
  margin-right:10px;
  font-weight:bold;
  color:#666;
}
div.jp-type-playlist div.jp-playlist a.jp-playlist-item-remove:hover {
  color:#0d88c1;
}
div.jp-type-playlist div.jp-playlist span.jp-free-media {
  float:right;
  display:inline;
  text-align:right;
  margin-right:10px;
}
div.jp-type-playlist div.jp-playlist span.jp-free-media a{
  color:#666;
}
div.jp-type-playlist div.jp-playlist span.jp-free-media a:hover{
  color:#0d88c1;
}
span.jp-artist {
  font-size:.8em;
  color:#666;
}

/* @end */

.jp-video-play {
  width:100%;
  overflow:hidden; /* Important for nested negative margins to work in modern browsers */
  cursor:pointer;
  background-color:rgba(0,0,0,0); /* Makes IE9 work with the active area over the whole video area. IE6/7/8 only have the button as active area. */
}
.jp-video-270p .jp-video-play {
  margin-top:-270px;
  height:270px;
}
.jp-video-360p .jp-video-play {
  margin-top:-360px;
  height:360px;
}
.jp-video-full .jp-video-play {
  height:100%;
}
.jp-video-play-icon {
  position:relative;
  display:block;
  width: 112px;
  height: 100px;

  margin-left:-53px;
  margin-top:-9px;
  left:50%;
  top:50%;

  background: url("jplayer.blue.monday.video.play.png") 0 0 no-repeat;
  text-indent:-9999px;
  border:none;
  cursor:pointer;
}
.jp-video-play-icon:focus {
  background: url("jplayer.blue.monday.video.play.png") 0 -100px no-repeat;
}


.jp-jplayer audio,
.jp-jplayer {
  width:0px;
  height:0px;
}

.jp-jplayer {
  background-color: #000000;
}



/* @group TOGGLES */

/* The audio toggles are nested inside jp-time-holder */

.jp-toggles {
  padding:0;
  margin:0 auto;
  overflow:hidden;
}

.jp-audio .jp-type-single .jp-toggles {
  width:25px;
}
.jp-audio .jp-type-playlist .jp-toggles {
  width:55px;
  margin: 0;
  position: absolute;
  left: 325px;
  top: 50px;
}

.jp-video .jp-toggles {
  /*	position:absolute;*/
  right:0px;


  position: absolute;
  height: 100%;
  width: 38px;
  border-left: 1px solid #999999;
}

.jp-toggles button {
  display:block;
  width:19px;
  height:19px;
  margin: 10px;
  padding: 0;

  text-indent:-9999px;
  line-height:100%; /* need this for IE6 */
  border:none;
  cursor:pointer;
}

.jp-full-screen {
  background: url("jplayer-nr.png") -6px -59px no-repeat;
}

.jp-full-screen:focus {
  background: url("jplayer-nr.png") -6px -59px no-repeat;
}

.jp-state-full-screen .jp-full-screen {
  background: url("jplayer-nr.png") -6px -59px no-repeat;
}

.jp-state-full-screen .jp-full-screen:focus {
  background: url("jplayer-nr.png") -6px -59px no-repeat;
}

.jp-repeat {
  background: url("jplayer.blue.monday.jpg") 0 -290px no-repeat;
}

.jp-repeat:focus {
  background: url("jplayer.blue.monday.jpg") -30px -290px no-repeat;
}

.jp-state-looped .jp-repeat {
  background: url("jplayer.blue.monday.jpg") -60px -290px no-repeat;
}

.jp-state-looped .jp-repeat:focus {
  background: url("jplayer.blue.monday.jpg") -90px -290px no-repeat;
}

.jp-shuffle {
  background: url("jplayer.blue.monday.jpg") 0 -270px no-repeat;
  margin-left: 5px;
}

.jp-shuffle:focus {
  background: url("jplayer.blue.monday.jpg") -30px -270px no-repeat;
}

.jp-state-shuffled .jp-shuffle {
  background: url("jplayer.blue.monday.jpg") -60px -270px no-repeat;
}

.jp-state-shuffled .jp-shuffle:focus {
  background: url("jplayer.blue.monday.jpg") -90px -270px no-repeat;
}


/* @end */

/* @group NO SOLUTION error feedback */

.jp-no-solution {
  padding:5px;
  font-size:.8em;
  background-color:#eee;
  border:2px solid #009be3;
  color:#000;
  display:none;
}

.jp-no-solution a {
  color:#000;
}

.jp-no-solution span {
  font-size:1em;
  display:block;
  text-align:center;
  font-weight:bold;
}

